import mapboxgl from "mapbox-gl"

class SliderOverlayControl implements mapboxgl.IControl {
  private sourceIds: string[]
  private map: mapboxgl.Map
  private container: HTMLDivElement

  constructor(sourceIds: string[], map: mapboxgl.Map) {
    this.sourceIds = sourceIds
    this.map = map
    this.container = document.createElement("div")
    this.container.className = "mapboxgl-ctrl mapboxgl-ctrl-top-left"

    this.createSliderOverlays()
  }
  private createSliderOverlays() {
    const sliders: Record<string, HTMLInputElement> = {}
    const mapOverlay = document.createElement("div")
    mapOverlay.className = "map-overlay top"
    mapOverlay.style.width = "200px"
    this.sourceIds.forEach((sourceId: string) => {
      const mapOverlayInner = document.createElement("div")
      mapOverlayInner.className = "map-overlay-inner"
      mapOverlayInner.style.display = "block"

      const label = document.createElement("label")
      // only add the dots if its too long
      if (sourceId.length <= 20) {
        label.textContent = sourceId
      } else {
        const shortenedsourceId = sourceId.substring(0, 20) + "..."
        label.textContent = shortenedsourceId
      }
      label.title = sourceId

      label.style.display = "flex" // Adjust the display style as needed

      const input = document.createElement("input")
      const sliderId = "slider-" + sourceId
      input.id = sliderId
      input.type = "range"
      input.min = "0"
      input.max = "100"
      input.step = "1"
      console.log(sourceId)
      if (sourceId.startsWith("maxar")) {
        input.value = "100"
      } else {
        input.value = "40"
      }
      input.style.width = "100%" // Add this line to make the slider element expand to its parent's width

      mapOverlayInner.appendChild(label)
      mapOverlayInner.appendChild(input)

      mapOverlay.appendChild(mapOverlayInner)

      sliders[sliderId] = input

      input.addEventListener("input", (e) => {
        console.log(window.map.getLayer(sourceId).type)

        const value = (e.target as HTMLInputElement).value
        if (window.map.getLayer(sourceId).type === "raster") {
          this.map.setPaintProperty(
            sourceId,
            "raster-opacity",
            parseInt(value, 10) / 100
          )
        } else if (window.map.getLayer(sourceId).type === "fill") {
          this.map.setPaintProperty(
            sourceId,
            "fill-opacity",
            parseInt(value, 10) / 100
          )
        }
      })
    })
    this.container.appendChild(mapOverlay)
  }

  addSourceId(sourceId: string) {
    this.sourceIds.push(sourceId)
    this.createSliderOverlays()
  }

  onAdd() {
    return this.container
  }

  onRemove() {
    this.container.parentNode?.removeChild(this.container)
  }
}

export { SliderOverlayControl }
