import mapboxgl from "mapbox-gl"
import MapboxDraw from "@mapbox/mapbox-gl-draw"
import { RenderData } from "streamlit-component-lib"

export class Raster {
  constructor(
    public filename: string,
    public height: number,
    public width: number,
    public src_crs: number,
    public src_transform: any,
    public crs: number,
    public bounds: [number, number, number, number],
    public base_dir: string,
    public transform: any,
    public raster_type: string
  ) {}

  public __dict__(): any {
    return {
      filename: this.filename,
      height: this.height,
      width: this.width,
      crs: this.crs,
      src_crs: this.src_crs,
      src_transform: this.src_transform,
      bounds: this.bounds,
      base_dir: this.base_dir,
      transform: this.transform,
      raster_type: this.raster_type,
    }
  }

  get png_filename(): string {
    return this.filename.replace(/\.[^/.]+$/, ".png")
  }
}
export class CartographerState {
  private static instance: CartographerState

  public location: GeoJSON.Position
  public zoom: number
  public viewport_bounds: number[]
  public rasters: Raster[]
  public feature_collection: GeoJSON.FeatureCollection
  public pointclouds: any[]
  public unique_labels: string[]
  public selected_label: string

  private constructor() {
    // Initialize the properties with default values
    this.location = [0, 0]
    this.zoom = 0
    this.viewport_bounds = [0, 0, 0, 0]
    this.rasters = []
    this.feature_collection = { type: "FeatureCollection", features: [] }
    this.pointclouds = []
    this.unique_labels = []
    this.selected_label = ""
  }

  public static getInstance(): CartographerState {
    if (!CartographerState.instance) {
      CartographerState.instance = new CartographerState()
    }
    return CartographerState.instance
  }

  public static initializeFromEvent(data: RenderData): void {
    const cartographerData = data.args["cartographer_state"]
    // Here we perform input validation before assigning the data to the singleton instance
    if (cartographerData && this.isValidCartographerData(cartographerData)) {
      const instance = this.getInstance()
      instance.location = cartographerData.location
      instance.zoom = cartographerData.zoom
      instance.viewport_bounds = cartographerData.viewport_bounds
      instance.rasters = cartographerData.rasters.map(
        (rasterData: any) =>
          new Raster(
            rasterData.filename,
            rasterData.height,
            rasterData.width,
            rasterData.crs,
            rasterData.src_crs,
            rasterData.src_transform,
            rasterData.bounds,
            rasterData.base_dir,
            rasterData.transform,
            rasterData.raster_type
          )
      )
      console.log(cartographerData.feature_collection)
      instance.feature_collection = cartographerData.feature_collection
      instance.pointclouds = cartographerData.pointclouds
      instance.unique_labels = cartographerData.unique_labels
      instance.selected_label = cartographerData.selected_label
    } else {
      throw new Error(
        "USER CODE ERROR: Throwing because we received invalid data from the server" +
          JSON.stringify(cartographerData)
      )
    }
  }

  private static isValidCartographerData(data: any): boolean {
    const requiredKeys = [
      "location",
      "zoom",
      "viewport_bounds",
      "rasters",
      "feature_collection",
      "pointclouds",
      "unique_labels",
      "selected_label",
    ]

    return (
      data &&
      requiredKeys.every((key) =>
        Object.prototype.hasOwnProperty.call(data, key)
      )
    )
  }
  public updateViewportFromMap(map: any): void {
    let width = 1024
    let height = 660
    var cUR = map.unproject([width, 0]).toArray()
    var cLL = map.unproject([0, height]).toArray()
    var coordinates = [cLL, cUR]
    var center = map.unproject([width / 2, height / 2]).toArray()

    this.location = center as GeoJSON.Position
    this.zoom = map.getZoom()
    this.viewport_bounds = coordinates.flat()
  }
  public __dict__(): {} {
    // translate the raster objects to dictionaries
    return {
      location: this.location,
      zoom: this.zoom,
      viewport_bounds: this.viewport_bounds,
      rasters: this.rasters.map((raster) => raster.__dict__()),
      feature_collection: this.feature_collection,
      pointclouds: this.pointclouds,
      unique_labels: this.unique_labels,
      selected_label: this.selected_label,
    }
  }
}
declare global {
  interface Window {
    state: CartographerState
    map_initialized: boolean
    MAPILLARY_API_KEY: string
    map: mapboxgl.Map
    draw: MapboxDraw
    sources: Set<string>
    palette: { [str: string]: string }
    draw_ctrl_visible: boolean
  }
}
