import mapboxgl from "mapbox-gl"
import MapboxDraw from "@mapbox/mapbox-gl-draw"
import { so_stylish } from "./styles"
import { GeoJSON } from "geojson"

export async function addMapillaryFeature(
  map: mapboxgl.Map,
  feature_collection: GeoJSON.FeatureCollection,
  className: string
) {
  // Query the features from the source
  const features = feature_collection.features
  const filteredFeatures = features.filter(
    (feature) =>
      feature.properties &&
      feature.properties.type === "mapillary_feature" &&
      feature.properties.description === className
  )

  let featureCollectionSubset = {
    type: "FeatureCollection",
    features: filteredFeatures,
  } as GeoJSON.FeatureCollection
  let source = map.getSource(className)

  if (source) {
    // @ts-ignore
    source.setData(featureCollectionSubset)
  } else {
    console.log("adding source for " + className)
    console.log(featureCollectionSubset.features.length)
    map.addSource(className, {
      type: "geojson",
      data: featureCollectionSubset,
    })

    const img = new Image()
    img.onload = function () {
      map.addImage(className, img)
      map.addLayer({
        id: className + "-layer",
        type: "symbol",
        source: className,
        layout: {
          "icon-image": className,
          "icon-size": 0.15,
          "icon-allow-overlap": true,
        },
      })
    }
    var svgUrl = `https://raw.githubusercontent.com/mapillary/mapillary_sprite_source/master/package_objects/${className}.svg`;

    fetch(svgUrl)
      .then(response => response.text())
      .then(svgContent => {
        const encodedSvg = btoa(svgContent);
        const dataUri = `data:image/svg+xml;base64,${encodedSvg}`;
        img.src = dataUri;
      })
      .catch(error => {
        console.error("Error fetching SVG:", error);
      });
  }
}

export function addGeoJSONLayers(
  map: mapboxgl.Map,
  sourceIds: string[],
  featureCollection: GeoJSON.FeatureCollection
) {
  var addedSourceIds = new Set<string>()
  for (const sourceId of sourceIds) {
    let source = map.getSource(sourceId)
    let featureCollectionSubset = {
      type: "FeatureCollection",
      features: featureCollection.features.filter((feature) => {
        return feature.properties?.description === sourceId
      }),
    } as GeoJSON.FeatureCollection
    if (featureCollectionSubset.features.length == 0) {
      continue
    }
    if (source) {
      // @ts-ignore
      source.setData(featureCollectionSubset)
      addedSourceIds.add(sourceId)
    } else {
      map.addSource(sourceId, {
        type: "geojson",
        data: featureCollectionSubset,
      })
      let fillColor = featureCollectionSubset.features[0].properties?.color
      map.addLayer({
        id: sourceId,
        type: "fill",
        source: sourceId,
        paint: {
          "fill-color": fillColor,
          "fill-opacity": 0.4,
        },
        filter: ["==", "$type", "Polygon"],
      })
    }
    addedSourceIds.add(sourceId)
  }
  return addedSourceIds
}

export function buildDrawPlugin() {
  window.map.removeControl(window.draw)
  const new_draw = new MapboxDraw({
    displayControlsDefault: true,
    controls: {
      polygon: true,
      trash: true,
      combine_features: false,
      uncombine_features: false,
    },
    styles: so_stylish,
    userProperties: true,
  })

  window.map.addControl(new_draw)
  return new_draw
}
