import { RenderData, Streamlit } from "streamlit-component-lib"
import mapboxgl from "mapbox-gl"
import "mapbox-gl/dist/mapbox-gl.css"
import MapboxDraw from "@mapbox/mapbox-gl-draw"
import { so_stylish } from "./styles"
import { Raster, CartographerState } from "./types"
import { SliderOverlayControl } from "./interaction_handling"
import {
  addGeoJSONLayers,
  addMapillaryFeature,
  buildDrawPlugin,
} from "./mapbox"
import {
  cornerBounds,
  assignColorsToFeatures,
  buildColorPalette,
  configureServices,
} from "./utilities"

configureServices()
let styleLoaded = false

function updateBackendWithState(): void {
  Streamlit.setComponentValue(window.state.__dict__())
}

const BASE_URL = process.env.REACT_APP_BASE_URL;

function addRasterToMap(map: any, raster: Raster) {
  console.log("/app/static/" + raster.png_filename)
  map.addSource(raster.png_filename, {
    type: "image",
  url: BASE_URL + "/app/static/" + raster.png_filename,
    coordinates: cornerBounds(raster.bounds),
  })
  if (raster.raster_type === "predicted_overlay") {
    map.addLayer({
      id: raster.png_filename,
      type: "raster",
      source: raster.png_filename,
      paint: {
        "raster-fade-duration": 0,
        "raster-opacity": 0.4,
      },
    })
  } else if (raster.raster_type === "base_satellite_image") {
    map.addLayer({
      id: raster.png_filename,
      type: "raster",
      source: raster.png_filename,
      paint: {
        "raster-fade-duration": 0,
        "raster-opacity": 1.0,
      },
    })
  }

  window.sources.add(raster.png_filename)
}

function removeRasters(map: mapboxgl.Map) {
  window.sources.forEach((source) => {
    if (
      source !== "mapbox-dem" &&
      source !== "mapillary" &&
      source.startsWith("prediction")
    ) {
      map.removeLayer(source)
      map.removeSource(source)
      window.sources.delete(source)
    }
  })
}

function updateMapWithRasters(map: mapboxgl.Map, rasters: Raster[]) {
  removeRasters(map)
  for (let i = 0; i < rasters.length; i++) {
    let raster = rasters[i]
    // if its the predicted overlay
    if (raster.raster_type === "predicted_overlay") {
      if (rasters.length === 3 && i === 2) {
        addRasterToMap(map, raster)
      } else if (rasters.length === 2 && i === 1) {
        addRasterToMap(map, raster)
      }
    }
    // if its the ground truth overlay
    else if (
      raster.raster_type === "base_satellite_image" &&
      !window.sources.has(raster.png_filename)
    ) {
      addRasterToMap(map, raster)
    }
  }
  var rasterSourceIds = rasters.map((raster) => raster.png_filename)
  const sliderOverlayControl = new SliderOverlayControl(rasterSourceIds, map)
  map.addControl(sliderOverlayControl, "top-left")

  window.state.updateViewportFromMap(map)
  // updateBackendWithState()
}

function initializeMap(state: CartographerState): mapboxgl.Map {
  let map = new mapboxgl.Map({
    container: "map",
    style: "mapbox://styles/mapbox/satellite-streets-v12",
    center: new mapboxgl.LngLat(state.location[0], state.location[1]),
    zoom: 17,
  })
  window.map = map

  window.draw = new MapboxDraw({
    displayControlsDefault: false,
    controls: {},
    styles: so_stylish,
    userProperties: true,
  })
  window.map.addControl(window.draw)
  window.map_initialized = false
  window.state.updateViewportFromMap(map)
  window.palette = {}
  window.sources = new Set<string>()
  window.draw_ctrl_visible = false

  map.on("load", () => {
    styleLoaded = true
    map.addSource("mapbox-dem", {
      type: "raster-dem",
      url: "mapbox://mapbox.mapbox-terrain-dem-v1",
      tileSize: 512,
      maxzoom: 14,
    })
    map.setTerrain({ source: "mapbox-dem", exaggeration: 1.5 })
    addMapillaryFeature(
      map,
      window.state.feature_collection,
      "object--support--utility-pole"
    )
    addMapillaryFeature(
      map,
      window.state.feature_collection,
      "object--sign--store"
    )

    updateMapWithRasters(map, window.state.rasters)
  })
  window.palette = buildColorPalette(state.unique_labels)

  assignColorsToFeatures(state.feature_collection.features)

  map.on("draw.create", function (e) {
    console.log("draw.create")
    e.features[0].type = "Feature"
    e.features[0].properties = {
      color: window.palette[window.state.selected_label],
      description: window.state.selected_label,
      type: "click",
    }
    window.draw.add(e.features[0])
    window.state.feature_collection.features.push(e.features[0])
    console.log(window.state.feature_collection.features)
    window.state.updateViewportFromMap(map)
    var vectorSourceIds = addGeoJSONLayers(
      window.map,
      window.state.unique_labels,
      window.state.feature_collection
    )
    var rasterSourceIds = window.state.rasters.map(
      (raster) => raster.png_filename
    )
    var sourceIds = [...rasterSourceIds, ...Array.from(vectorSourceIds)]

    const sliderOverlayControl = new SliderOverlayControl(sourceIds, window.map)
    window.map.addControl(sliderOverlayControl, "top-left")

    updateBackendWithState()
  })
  map.on("draw.update", function (e) {
    console.log("draw.update")
    var features = [
      ...window.draw.getAll().features,
      ...window.state.feature_collection.features,
    ]
    // make them a set then array to remove duplicates
    window.state.feature_collection.features = Array.from(new Set(features))
    window.state.updateViewportFromMap(map)
    updateBackendWithState()
  })
  map.addControl(
    new mapboxgl.ScaleControl({ maxWidth: 80, unit: "metric" }),
    "bottom-right"
  )
  return map
}

function onRender(event: Event): void {
  const data = (event as CustomEvent<RenderData>).detail
  CartographerState.initializeFromEvent(data)
  window.state = CartographerState.getInstance()
  window.palette = buildColorPalette(window.state.unique_labels)
  if (!window.map_initialized) {
    window.map = initializeMap(window.state)
    window.map_initialized = true
  }
  if (
    window.state.unique_labels.length > 0 &&
    !window.draw_ctrl_visible &&
    styleLoaded
  ) {
    window.draw = buildDrawPlugin()
    window.draw_ctrl_visible = true
  }

  // check whether the list of raster png_filenames is the same as those in sources
  let changed = false
  for (let i = 0; i < window.state.rasters.length; i++) {
    let raster = window.state.rasters[i]
    if (!window.sources.has(raster.png_filename)) {
      changed = true
    }
  }
  if (styleLoaded && changed) {
    updateMapWithRasters(window.map, window.state.rasters)
  }

  if (
    styleLoaded &&
    window.map_initialized &&
    window.state.feature_collection.features.length > 0
  ) {
    var vectorSourceIds = addGeoJSONLayers(
      window.map,
      window.state.unique_labels,
      window.state.feature_collection
    )
    var rasterSourceIds = window.state.rasters.map(
      (raster) => raster.png_filename
    )
    var sourceIds = [...rasterSourceIds, ...Array.from(vectorSourceIds)]

    const sliderOverlayControl = new SliderOverlayControl(sourceIds, window.map)
    window.map.addControl(sliderOverlayControl, "top-left")
  }
}

// Attach our `onRender` handler to Streamlit's render event.
Streamlit.events.addEventListener(Streamlit.RENDER_EVENT, onRender)

// Tell Streamlit we're ready to start receiving data. We won't get our
// first RENDER_EVENT until we call this function.
Streamlit.setComponentReady()

// Finally, tell Streamlit to update our initial height. We omit the
// `height` parameter here to have it default to our scrollHeight.
Streamlit.setFrameHeight()
