import { GeoJSON } from "geojson"
import mapboxgl from "mapbox-gl"

function cornerBounds(bounds: [number, number, number, number]): number[][] {
  return [
    // [ (lu), (ru), (rd), (ld) ]
    [bounds[0], bounds[3]],
    [bounds[2], bounds[3]],
    [bounds[2], bounds[1]],
    [bounds[0], bounds[1]],
  ]
}

function assignColorsToFeatures(
  features: GeoJSON.Feature<GeoJSON.Geometry, any>[]
): void {
  features.forEach((feature) => {
    const color = window.palette[feature.properties.description]
    feature.properties = {
      ...feature.properties,
      color,
    }
  })
}

function buildColorPalette(unique_labels: string[]): any {
  const colorList = [
    "#FF6F40", // Coral
    "#1CA953", // Forest Green
    "#0077B6", // Cerulean Blue
    "#FFC300", // Vivid Yellow
    "#B7410E", // Rust
    "#2F4F4F", // Slate Gray
    "#800080", // Purple
    "#FF55A3", // Fuchsia Pink
  ]

  const palette: { [string: string]: string } = {}
  unique_labels.forEach((label, index) => {
    palette[label] = colorList[index % colorList.length]
  })
  return palette
}
